.home_main {
  display: flex;
  background-color: #0093e9;
  background-image: linear-gradient(160deg, #d3dce1 0%, #c4d7d5 100%);
  width: 100%;
}
.home_left_bar {
  height: 50vh;
  width: 14%;
  position: fixed;
  background-color: white;
  border: 1px solid;
  border-bottom-left-radius: 1.2rem;
  border-bottom-right-radius: 1.2rem;
}
.type_of_product {
  font-size: 1.2rem;
  width: 70%;
  margin: auto;

  margin-top: 3vh;
  padding-left: 1%;
  padding-right: 1%;
  border-radius: 1.3rem;
  color: aliceblue;

  background-image: url(../../images/bark.png);
}
.type_of_product:hover {
  cursor: pointer;
}
.home_right_main {
  width: 78%;

  margin-left: 20%;
}
.home_heading_box {
  width: 48%;
  margin: auto;
  border-radius: 2.1rem;
  background-image: url(../../images/bark.png);
  margin-bottom: 1%;
  margin-top: 2%;
}
.home_heading_slogan {
  font-size: 1.1rem;
  font-style: initial;
  color: rgb(255, 255, 255);

  font-weight: 700;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.home_right_slider {
  border: 2px solid black;
  background-image: url(../../images/slide_1.jpg);
  width: 80%;
  height: 60vh;
  border-radius: 1.2rem;
  margin-left: 8%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  background-size: 100% 100%;
  animation: changeImage 20s 2s alternate infinite;
}
@keyframes changeImage {
  0% {
    background-image: url(../../images/slide_1.jpg);
  }
  25% {
    background-image: url(../../images/slide_2.jpg);
  }
  50% {
    background-image: url(../../images/slide_3.jpg);
  }
  75% {
    background-image: url(../../images/slide_4.jpg);
  }
  100% {
    background-image: url(../../images/slide_5.png);
  }
}

.Eco_kitchen {
  font-size: 1.4rem;
  margin-right: 80%;
  margin-top: 3vh;
  padding-left: 1%;
  padding-right: 1%;
  border-radius: 1.3rem;
  color: aliceblue;
  background-image: url(../../images/bark.png);
}
.Eco_kitchen_box {
  display: grid;
  background-color: white;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

@media all and (min-width: 701px) and (max-width: 1030px) {
  .home_right_slider {
    height: 40vh;
  }
  .home_heading_box {
    width: 55%;
  }
  .home_heading_slogan {
    font-size: 90%;
  }
  .home_left_bar {
    width: 20%;
  }
  .home_right_main {
    width: 76%;
    margin-left: 23%;
  }
  .Eco_kitchen_box {
    grid-template-columns: 1fr 1fr 1fr;
  }
  .Eco_kitchen {
    margin-right: 75%;
  }
}
/* 
hi */
@media all and (min-width: 400px) and (max-width: 701px) {
  .home_right_slider {
    height: 30vh;
  }
  .home_heading_slogan {
    font-size: 70%;
  }

  .Eco_kitchen {
    margin-right: 57%;
  }
  .home_heading_box {
    width: 75%;
    margin-bottom: 5%;
  }
  .Eco_kitchen_box {
    margin-left: 3%;
    grid-template-columns: 1fr 1fr;
  }

  .home_left_bar {
    width: 20%;
  }
}
@media all and (min-width: 0px) and (max-width: 399px) {
  .home_right_slider {
    height: 20vh;
  }
  .home_heading_slogan {
    font-size: 60%;
  }
  .Eco_kitchen {
    margin-right: 1%;
  }

  .Eco_kitchen_box {
    grid-template-columns: 1fr;
  }

  .home_right_main {
    width: 95%;
    margin-left: 0%;
  }

  .home_left_bar {
    display: none;
    width: 0%;
    height: 0vh;
    background-color: rgb(240, 255, 244);
  }

  .type_of_product {
    width: 100%;
  }
  .Eco_kitchen_box {
    grid-template-columns: 1fr 1fr;
  }
  .Eco_kitchen {
    margin-right: 1%;
  }
}
@media all and (min-width: 400px) and (max-width: 600px) {
  .home_left_bar {
    display: none;
    width: 0%;
    height: 0vh;
    background-color: rgb(240, 255, 244);
  }
  .home_right_main {
    width: 95%;
    margin-left: 0%;
  }
}
